// --- movie-modal__content ---

.movie-modal__content {
  @include tablet {
    display: flex;
    align-items: flex-start;
  }
}

.movie-modal__img {
  border-radius: 5px;

  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }
  @include tablet {
    width: 264px;
    margin-right: 68px;
  }

  @include desktop {
    width: 375px;
    margin-right: 16px;
  }
}

.movie-modal__text {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    max-width: 260px;
  }
}
.movie-modal__title {
  margin-bottom: 20px;
  font-size: 20px;
  line-height: 1.15;
  font-weight: 500;
  text-transform: uppercase;

  @include desktop {
    font-size: 30px;
  }
}
.movie-modal__figures {
  display: flex;
  margin-bottom: 20px;
}

.movie-modal__properties {
  flex-shrink: 0;
  margin-right: 40px;
  color: $lightTextColor;
  @include desktop {
    margin-right: 80px;
  }

  & li:not(:last-child) {
    margin-bottom: 8px;
  }
}

.movie-modal__values {
  &:first-child {
    color: $lightTextColor;
  }
  & li:not(:last-child) {
    margin-bottom: 8px;
  }
}

.movie-modal__vote-average {
  padding: 1px 9px;
  color: $whiteColor;
  background-color: $accentColor;
  border-radius: 5px;
}

.movie-modal__vote-count {
  padding: 1px 5px;
  background-color: $bgColor;
  border-radius: 5px;
}

.movie-modal__about--title {
  font-weight: 500;
  margin-bottom: 8px;
  text-transform: uppercase;
}

.movie-modal__about {
  margin-bottom: 20px;
  line-height: 1.67;
}
